import * as React from "react";
import Helmet from "react-helmet";
import Utterances from "./utterances";

import "./global.css";
import OctoCat from "./octo-cat";
import "./post.css";

// https://www.gatsbyjs.com/docs/how-to/routing/customizing-components/
// https://reacttraining.com/blog/gatsby-mdx-blog/
export default function Layout({ children, pageContext }) {
  const { title, description, keywords, url, image, date } =
    pageContext.frontmatter;

  return (
    <>
      <header>
        <a href="/">Home</a>
      </header>
      <div id="post-body" className="markdown-body dark">
        <OctoCat />
        <Helmet>
          <title>{title}</title>

          <meta name="description" content={description} />
          <meta name="keywords" content={keywords} />
          <meta name="author" content="doong-jo"></meta>

          <meta property="og:type" content="website" />
          <meta property="og:title" content={title} />
          <meta property="og:site_name" content={title} />
          <meta property="og:description" content={description} />
          <meta property="og:image" content={image} />
          <meta property="og:url" content={url} />

          <meta name="twitter:title" content={title} />
          <meta name="twitter:description" content={description} />
          <meta name="twitter:image" content={image} />

          <link rel="canonical" href={url} />
        </Helmet>
        <time>{date}</time>
        {children}
        <hr />
        <div>
          <p>
            글이 유익하셨다면{" "}
            <a href="https://vsvaz7hhr1h.typeform.com/to/TF2iR7HX">
              최대 30초 걸리는 설문
            </a>
            에 참여해주실 수 있을까요? 🙂
          </p>
          <details>
            <summary>어떤 설문인가요?</summary>
            프론트엔드 개발자가 실무에서 만들게 되는 내부 툴을 조사하고
            있습니다. 많은 회사의 프론트엔드 개발자들은 해결하고자 하는 문제는
            같지만 그 구현은 조금씩 다르게 내부 툴을 만들어 왔습니다. 나의 바로
            옆 회사의 개발자는 나와 똑같은 것을 만들고 있을지도 모릅니다. 저는
            이러한 비효율을 해결하고 싶으며 프론트엔드 개발자들이 더 중요한 일에
            집중하게 돕고 싶습니다.
          </details>
        </div>
        <Utterances />
      </div>
    </>
  );
}
